import StatefulComponent from "../StatefulComponent";
import { connect } from "react-redux";
import PurchaseLimits from "./PurchaseLimits";
import { toast } from "react-toastify";

class PurchaseForm extends StatefulComponent {
    constructor(props) {
        super(props);

        this.handleChange = this.handleChange.bind(this);
        this.buy = this.buy.bind(this);
        this.state = {
            investAmount: "",
            returnAmount: "",
            checkPolicy: false,
        }
    }

    buy() {
        if (Number(this.state.investAmount) < 100) {
            toast.info("Please confirm your invest amount. It should be bigger than $100", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }
        if (!this.state.checkPolicy) {
            toast.info("Please confirm terms and policy", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        this.props.dispatch({ type: "BUY_TOKEN", payload: { investTokenAmount: this.state.investAmount } });
    }

    handleChange(event) {
        var value = event.target.value;
        var pattern = /[^0-9]/g;
        let result = value.match(pattern);
        if (!value || !result) {
            this.setState({ investAmount: event.target.value });
            this.props.dispatch({
                type: 'GET_TOKEN_AMOUNT', payload: { investTokenAmount: event.target.value }
            });
        }
    }

    render() {
        return (
            <>
                <div className="purchase-form wallet-connection">
                    {/* <PurchaseLimits/> */}
                    <div className="purchase-amount">
                        {/* <form> */}
                        <div className="account-address">{this.props.account.slice(0, 5) + "..." + this.props.account.slice(38)}</div>
                        <div className="newInputs">
                            <div className="leftInputs NewHolder">
                                <div className="amoutToken"><label>Enter amount</label></div>
                                <div className="newInputsItem">
                                    <input className={this.props.returnCoinAmount > 0 ? "input-warning active" : "input-warning"} type="text" placeholder="1000"
                                        value={this.state.investAmount || ""}
                                        onChange={this.handleChange} />
                                    <button className="selectDinar">
                                        <div className="optionDinar" >
                                            <div className="imageDinar"><img alt="USDC.e" src="https://raw.githubusercontent.com/ava-labs/avalanche-bridge-resources/main/tokens/USDC/logo.png" /></div>
                                        </div>
                                        <div className="">{this.props.investToken}</div>
                                        {/* 
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true" width="18">
                                                <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd"></path>
                                            </svg>
                                            */}
                                    </button>
                                </div>
                                <PurchaseLimits compact={true} />
                            </div>

                            <div className="rightInputs NewHolder">

                                <div className="amoutToken"><label>{this.props.returnToken} token</label></div>
                                <div className="newInputsItem">
                                    {/* TODO: autofill token amount based on purchase amount */}
                                    <input className="disablePointer" type="text" placeholder="Autofill"
                                        value={this.props.returnTokenAmount} onChange={() => { }} readOnly />
                                    <button className="selectDinar">
                                        <div className="optionDinar" >
                                            <div className="imageDinar"><img src="/img/swapsicle-coin.png" alt="Swapsicle token" /></div>
                                        </div>
                                        <div className="">pPOP</div>
                                    </button>
                                </div>
                                {/* <div className="leftBottom">Token price: $0.20</div> */}
                            </div>

                        </div>

                        {/* <div className="selectCtry">
                                <label>
                                    Select country
                                    <select>
                                        <option>So many options</option>
                                        <option>...</option>
                                    </select>
                                </label>
                            </div> */}
                        <div className="checkBoxr">
                            <p className="buyP">If your account is:</p>
                            <ul className="buyInfo">
                                <li>NFT holder, Maximum of <span className="fffPrice">200,000</span> <span className="popsColr">pPOPS</span> at&nbsp;<span className="fffPrice">$0.20</span></li>    
                                <li>Nest holder, Maximum of <span className="fffPrice">50,000</span> <span className="popsColr">pPOPS</span> at&nbsp;<span className="fffPrice">$0.40</span></li>
                                <li>Not one of the above, Maximum of <span className="fffPrice">20,000</span> <span className="popsColr">pPOPS</span> at&nbsp;<span className="fffPrice">$0.50</span></li>
                            </ul>                             

                                    {/* If your account is<br></br>
                                    - NFT holder, Maximum of 200, 000 pPOPS at $0.20<br></br>
                                    - Nest holder, Maximum of 50, 000 pPOPS at $0.40<br></br>
                                    - Not above one, Maximum of 20, 000 pPOPS at $0.50<br></br> */}
                                
                            <div className="checkBoxr">
                                <label>
                                    <input type="checkbox" checked={this.state.checkPolicy}
                                        onChange={() => { this.setState({ checkPolicy: !this.state.checkPolicy }) }} />
                                        I have read the Terms &amp; Conditions
                                </label>
                            </div>
                            <p className="buyPsmall">
                            This offering is only available to non-U.S. persons. This offering has not been registered under the U.S. Securities Act of 1933 (“Securities Act”), as amended, and may not be offered or sold in the United States or to a U.S. person (as defined in Regulation S promulgated under the Securities Act) absent registration or an applicable exemption from the registration requirements.
                            Cryptoassets are not regulated and involve a high degree of risk – you could lose all of the capital invested. Cryptoassets do not have any protection from any compensation scheme in the event of their failure. If you are unsure whether investing in cryptoassets is suitable for your circumstances you should seek independent professional advice prior to investing. Capital at risk. Terms and conditions may be subject to change without notice.
                            </p>
                            <button className="connectWallet" onClick={this.buy}>Buy now</button>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

const mapStateToProps = state => {
    console.log("state: ", state);
    return {
        investAmount: state.investAmount,
        investToken: state.investToken,
        investTokenAmount: state.investTokenAmount,
        returnTokenAmount: state.returnTokenAmount,
        returnToken: state.returnToken,
        returnCoinAmount: Number(state.returnCoinAmount),
        account: state.account
    };
}

const mapDispatchToProps = dispatch => {
    return {
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PurchaseForm);
